$icon: 1.25em;

.footer {
    .phone {
        float: left;
        margin-right: 2em;
        width: 260px;
        .tel {
            font-size: 24px;
            line-height: 30px; }
        &__desc {
            color: #949494;
            font-size: 15px; }
        @include md {
            display: none; } }

    &__soc--md {
        display: none;
        @include clr;
        @include md {
            display: block; }
        .soc li {
            margin-bottom: 1em; } }

    &__nav, &__nav2 {
        float: left;
        width: 13%;
        margin-right: 1em;
        @include sm {
            width: 40%;
            margin-right: 1%; }
        h3 {
            font-size: 15px;
            margin-bottom: 15px; }
        a {
            color: #949494; }
        li {
            margin-bottom: 1em;
            font-size: 16px;
            line-height: 14px; } }
    .soc {
        li {
            float: left;
            margin-right: .5em; }
        .icon {
            width:  $icon;
            height: $icon; } }

    &__nav2 {
        @include md {
            display: none; } } }
