        color: #333;
        font-size: 18px;
        margin-bottom: 30px;
        text-transform: uppercase;
        font-weight: bold;
        &__item {
            text-align: right;
            margin-bottom: 10px;
            position: relative;
            .submenu {
                margin: 1em 0;
                font-weight: normal;
                color: #000;
                a {
                    color: #000;
                    font-size: 16px; } } } }
    .sale a {
        color: #f24b4b; }
    .userArea {
        .menu__item {
            font-weight: normal;
            font-size: 16px; } }
    .location {
        .menu__item {
            font-weight: normal;
            font-size: 16px; } }
