@import "header_promo";
header {
    font-size: 16px;
    color: #949494;
    @include md;

    .logo {
        float: left;
        display: block;

        width: 225px;
        height: 60px;
        //margin: 2em 0;

        margin-right: 3em;
        @include sm {
            width: 140px;
            margin: 0; }
        span,
        .pic {
            text-indent: -1984px;
            display: block;
            height: 50px;
            background: url('../img/logo.png') no-repeat 50% 50%;
            background-size: contain; }
        small,
        .title {
            display: block;
            text-align: center;
            font-size: 13px;
            height: 15px;
            line-height: 15px;
            margin-top: -5px;
            color: #ababab;
            letter-spacing: .05em;
            @include sm {
                display: none; } } }


    .location {
        float: left;
        margin-right: 1em;
        margin-top: 1em;
        &--change {
            border-bottom: 1px dashed #bbb;
            position: relative;
            margin-left: 15px;
            .fa {
                position: absolute;
                left: -13px; } }
        @include md {
            display: none; } }
    .header__right {
        float: right; }
    .userArea {
        float: left;
        margin-right: 1em;
        margin-top: 1.25em;
        position: relative;
        @include sm {
            margin-top: 1.1em; }

        &__sign {
            .icon-sign {
                fill: #000;
                display: none;
                @include sm {
                    display: block; } }
            &-title {
                @include sm {
                    display: none; } } } }
    .basket {
        float: left;
        margin-right: 2em;
        margin-top: 1.25em;
        @include sm {
            margin-top: 1em; }
        &--order {
            background: url('../img/basket.svg') no-repeat right 0;
            position: relative;
            display: block;
            padding-right: 25px;
            height: 25px; }
        &-title {
            display: block;
            @include sm {
                display: none; } }
        &--count {
            position: absolute;
            right: -10px;
            top: 0;

            border-radius: 50%;
            background: #000;
            color: #fff;
            font-size: 10px;
            width: 20px;
            line-height: 20px;
            height: 20px;
            text-align: center; } } }



.js-toggle-nav {
    float: left;
    display: block;
    margin-top: 14px;
    font-size: 7px;
    background: url('../img/nav_open.svg') no-repeat 0 0;
    width: 25px;
    height: 25px;
    display: none;
    &.is-active {
        background: url('../img/nav_close.svg') no-repeat 0 0; }
    @include sm {
        display: block; } }
