.container {
    max-width: 1220px;
    margin: 0 auto;
    padding: 0 20px;
    position: relative;
    overflow: hidden;
    @include sm {
        padding: 0 0; }
    &__in {
        max-width: 580px;
        margin: 0 auto; } }

.wrapper {
    padding: 0 50px;
    @include sm {
        padding: 0 15px; } }

.row {
    @include clr;
    margin: 0 -20px -25px;
    @include sm {
        margin-left: -15px;
        margin-right: -15px; }
    > div {
        margin-bottom: 25px;
        padding: 0 20px;
        float: left;
        @include sm {
            padding: 0 15px; } } }
.out {
    position: relative;
    overflow: hidden;
    @include clr; }


aside.sidebar {
    float: left;
    width: 285px;
    @include sm {
        display: none; } }

aside.sidebar_mobile {
    transform: translate(-400px, 0);
    position: fixed;
    top: 50px;
    padding: 1em;
    width: 10%;
    transition: width 0.5s cubic-bezier(0.15, 0.99, 0.18, 1.01);
    z-index: 999;

    .is-nav & {
        @include sm {
            transform: translate(0, 0);
            overflow-y:scroll {}
            width: 100%;
            left: 0;
            bottom: 0; } } }


header {
    position: relative;
    min-height: 80px;
    min-width: 320px;
    padding: 10px 0;

    background: rgba(242, 242, 242, .85);
    z-index: 1;
    box-shadow: none;
    margin-bottom: 0em;

    @include sm {
        padding: 0;
        //padding-right: 40px;
        width: 100%;
        height: 50px;
        min-height: 50px;
        box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
        position: fixed;
        z-index: 999;
        //top: -1px;
        background: #f5f5f5; } }

main {
    float: right;
    width: calc(100% - 285px);
    @include sm {
        float: none;
        width: 100%;
        padding: 1em; } }
footer {
    padding: 50px 0;
    //max-width: 1220px;
    border-top: 1px solid #E0E0E0;
    .footer__in {
        max-width: 1220px;
        margin: 0 auto;
        padding: 0 20px; }
    @include md {
        padding-top: 25px; }
    @include sm {
        padding: 1em; } }

.container {
    padding-top: 90px;
    header & {
        padding: 0 20px;
        @include sm {
            padding: 0 10px; } } }
header, aside {
    z-index: 999; }

header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100; }
